import {request} from "../../request.js";
export default {
	data() {
		return {
			banklist:[],
			bank_id:0,
			bank_name:"",
			idcard:"",
			is_def:"0",
			realname:"",
			mobile:"",
			captcha:"",
			cardnum:"",
			bank_id_list:[],
			bamkId:"",
		}
	},
	onLoad() {
		this.getBankList();
		
	},
	methods: {
		getBankList(){
			request({
				url:"index.php?s=/wap/index/getBank"
			}).then(res=>{
				for(let i in res.data){
					this.banklist.push(res.data[i]);
					this.bank_id_list.push(i);
				}
				this.bank_name=this.banklist[this.bank_id];
				this.bamkId = this.bank_id_list[this.bank_id];
				uni.hideLoading();
			})
		},
		changebank(event){
			this.bank_id = event.detail.value;
			this.bank_name=this.banklist[this.bank_id];
			this.bamkId=this.bank_id_list[this.bank_id];
			
		},
		chooseDef(){
			this.is_def = this.is_def ==0?"1":"0";
			console.log(this.is_def);
		},
		submit(){
			if(this.idcard ==""){
				uni.showToast({
					title:"身份证不允许为空!",
					icon:"none"
				})
				return
			}
			if(this.realname ==""){
				uni.showToast({
					title:"真实姓名不允许为空!",
					icon:"none"
				})
				return
			}
			if(this.mobile ==""){
				uni.showToast({
					title:"手机号不允许为空!",
					icon:"none"
				})
				return
			}
			if(this.captcha ==""){
				uni.showToast({
					title:"验证码不允许为空!",
					icon:"none"
				})
				return
			}
			if(this.cardnum ==""){
				uni.showToast({
					title:"银行卡账号不允许为空!",
					icon:"none"
				})
				return
			}
			uni.showLoading({
				title:"加载中",
				icon:"loading",
				mask:true
			})
			request({
				url:"index.php?s=/wap/member/bankCardBinding",
				method:"POST",
				data:{
					mobile:this.mobile,
					captcha:this.captcha,
					is_default:this.is_def,
					bank_name:this.banklist[this.bank_id],
					realname:this.realname,
					identification:this.idcard,
					account_number:this.cardnum,
					bank_id:this.bamkId
				}
			}).then(res=>{
				if(res.data.code >0){
					console.log(res);
					uni.hideLoading();
					uni.showToast({
						title:res.data.msg,
						icon:"none",
					})
					setTimeout(function(){
						uni.navigateTo({
							url:"../bank/bank"
						});
					},500);
				}else{
					console.log(res);
					uni.hideLoading();
					uni.showToast({
						title:res.data.msg,
						icon:"none",
					})
				}
			})
		},
		getCaptcha(){
			if(this.mobile==""){
				uni.showToast({
					title:"请先输入手机号码!",
					icon:"none"
				});
				return;
			}
			request({
				url:"index.php?s=/wap/index/sendBackCardBindingFly",
				data:{
					mobile:this.mobile
				}
			}).then(res=>{
				if(res.data.code>0){
					uni.showToast({
						title:res.data.msg,
						icon:"none",
					})
				}else{
					uni.showToast({
						title:res.data.msg,
						icon:"none",
					})
					return
				}
			})
		}
		
	}
}
